import React, { useState, useRef, useEffect } from 'react';
import axios from 'axios';
import { useDropzone } from 'react-dropzone';
import { Magnifier } from "react-image-magnifiers";
import { Chart } from 'chart.js/auto';

const API_URL = 'http://localhost:8000';

function App() {
  const [image, setImage] = useState(null);
  const [results, setResults] = useState(null);
  const [loading, setLoading] = useState(false);
  const canvasRef = useRef(null);
  const chartRef = useRef(null);
  const chartInstance = useRef(null);

  const { getRootProps, getInputProps } = useDropzone({
    accept: 'image/*',
    maxFiles: 1,
    onDrop: acceptedFiles => {
      const file = acceptedFiles[0];
      const reader = new FileReader();
      reader.onload = () => setImage(reader.result);
      reader.readAsDataURL(file);
    }
  });

  //   chart    
  useEffect(() => {
    return () => {
      if (chartInstance.current) {
        chartInstance.current.destroy();
      }
    };
  }, []);

  const analyzeImage = async () => {
    if (!image) return;
    
    setLoading(true);
    try {
      const file = dataURLtoFile(image, 'mri.jpg'); //   FormData
      const formData = new FormData();
      formData.append('file', file);
      
      const response = await axios.post(`${API_URL}/analyze`, formData, {
        headers: { 'Content-Type': 'multipart/form-data' }
      });
      
      setResults(response.data);
      renderHeatmap(response.data.heatmap);
      renderDiagnosisChart(response.data.predictions);
    } catch (error) {
      console.error('Analysis error:', error);
    } finally {
      setLoading(false);
    }
  };

  const renderHeatmap = (heatmapData) => {
    const canvas = canvasRef.current;
    if (!canvas) return;
    
    const ctx = canvas.getContext('2d');
    ctx.clearRect(0, 0, canvas.width, canvas.height); //  canvas
    
    const heatmap = new Image();
    heatmap.onload = () => {
      ctx.drawImage(heatmap, 0, 0, canvas.width, canvas.height);
    };
    heatmap.src = `data:image/png;base64,${heatmapData}`;
  };

  const renderDiagnosisChart = (predictions) => {
    const chartCtx = chartRef.current?.getContext('2d');
    if (!chartCtx) return;

    //   chart
    if (chartInstance.current) {
      chartInstance.current.destroy();
    }
    
    chartInstance.current = new Chart(chartCtx, {
      type: 'bar',
      data: {
        labels: ['', ' ', '', ''],
        datasets: [{
          label: ' ',
          data: predictions,
          backgroundColor: [
            '#4CAF50', '#FFC107', '#FF9800', '#F44336'
          ]
        }]
      },
      options: {
        scales: { y: { beginAtZero: true, max: 1 } }
      }
    });
  };

  const dataURLtoFile = (dataurl, filename) => {
    const arr = dataurl.split(',');
    const mime = arr[0].match(/:(.*?);/)[1];
    const bstr = atob(arr[1]);
    let n = bstr.length;
    const u8arr = new Uint8Array(n);
    while (n--) u8arr[n] = bstr.charCodeAt(n);
    return new File([u8arr], filename, { type: mime });
  };

  return (
    <div className="app">
      <h1> -</h1>
      
      <div className="upload-section" {...getRootProps()}>
        <input {...getInputProps()} />
        {image ? (
          <div className="image-preview">
            <Magnifier
              imageSrc={image}
              imageAlt="-"
              largeImageSrc={image}
            />
          </div>
        ) : (
          <p> -    </p>
        )}
      </div>

      <button 
        onClick={analyzeImage} 
        disabled={!image || loading}
        style={{ marginTop: '20px', padding: '10px 20px' }}
      >
        {loading ? '...' : ''}
      </button>

      {results && (
        <div className="results" style={{ marginTop: '30px' }}>
          <h2> </h2>
          
          <div className="heatmap-container" style={{ margin: '20px 0' }}>
            <h3>  (Grad-CAM)</h3>
            <canvas ref={canvasRef} width="224" height="224" style={{ border: '1px solid #ddd' }} />
          </div>
          
          <div className="diagnosis-chart" style={{ margin: '20px 0' }}>
            <h3> </h3>
            <canvas ref={chartRef} width="400" height="200" />
          </div>
          
          <div className="diagnosis-text" style={{ 
            backgroundColor: '#f8f9fa',
            padding: '15px',
            borderRadius: '5px'
          }}>
            <h3>:</h3>
            <p>{results.diagnosis} (: {(results.confidence * 100).toFixed(2)}%)</p>
          </div>
        </div>
      )}
    </div>
  );
}

export default App;